	<view class="login">
		<view class="login-tel">
			<view class="tel-main">
				<view class="close">
					<image class="close-img" src="../../static/img/close.png"></image>
				</view>
				<view class="logo">
					<image class="logp-img" src="../../static/logo.png"></image>
				</view>
				<view class="tel">手机号注册</view>
				<LoginOther></LoginOther>
				<view class="login-go">
					<view>已有账号，去登录</view>
					<image src="../../static/img/arrow-down-bold.png"></image>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import LoginOther from '@/components/login/login-other.vue'
	export default {
		name: "my-login",
		data() {
			return {
				form: {
					userPhone: '',
					password: ''
				},
			};
		},
		components:{
			LoginOther
		},
		methods: {
			async phoneLogin() {
				const loginInfo = {
					username: this.$data.userPhone,
					password: this.$data.password,
					loginType: 0
				}
				const {
					data: loginResult
				} = await uni.$http.post("/api/users/login", loginInfo)
				if (loginResult.meta.status !== 200) return uni.$showMsg('登录失败！')
				this.updateToken(loginResult.data.data)
			},
			weixinLogin() {
				uni.getUserProfile({
					desc: '必须授权才能使用',
					success: (res) => {
						console.log(res)
						console.log(res.userInfo.nickName)
						// this.updateUserInfo(res.userInfo)
						// this.getToken(res)
					},
					fail: (res) => {
						uni.showToast({
							title: '你取消了登录授权'
						})
					},
				})
			},
			async getToken(info) {
				// 调用微信登录接口
				const [err, res] = await uni.login().catch(err => err)
				// 判断是否 uni.login() 调用失败
				if (err || res.errMsg !== 'login:ok') return uni.$showError('登录失败！')
				//准备参数
				const query = {
					code: res.code,
					encryptedData: info.encryptedData,
					iv: info.iv,
					rawData: info.rawData,
					signature: info.signature
				}
				// 换取 token
				const {
					data: loginResult
				} = await uni.$http.post('', query)
				if (loginResult.meta.status !== 200) return uni.$showMsg('登录失败！')
				// 2. 更新 vuex 中的 token
				this.updateToken(loginResult.message.token)
			}
		}
	}
</script>

